* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}
#map {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}

h1 {
  margin: 0;
  padding: 0;
  font-size: 14px;
}

.slider {
  min-height: 50px;
  padding: 15px 40px 40px;
}

.noUi-pips-horizontal {
  height: 40px;
}

.slider-heading {
  display: flex;
  justify-content: space-between;
  padding: 5px 35px;
}

.noUi-horizontal .noUi-handle {
  background: gray;
}

.noUi-marker-horizontal.noUi-marker-large {
  height: 7px;
  width: 3px;
}
.noUi-value {
  color: #777777;
  font-size: 16px;
  font-weight: bold;
}

@media screen and (max-width: 400px) {
  .noUi-value {
    font-size: 12px;
  }
}

.map-overlay {
  font: 12px/20px "Helvetica Neue", Arial, Helvetica, sans-serif;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  margin: 0 auto;
}

.map-overlay .map-overlay-inner {
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  padding: 10px;
  border: 2px solid gray;
}

@media screen and (max-width: 400px) {
  .map-overlay {
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0px;
  }
  .map-overlay .map-overlay-inner {
    border: none;
    border-top: 1px solid gray;
  }
}

.map-overlay h2 {
  line-height: 24px;
  display: block;
  margin: 0 0 10px;
}

.map-overlay .legend .bar {
  height: 10px;
  width: 100%;
  background: linear-gradient(to right, #fca107, #7f3121);
}

.map-overlay input {
  background-color: transparent;
  display: inline-block;
  width: 100%;
  position: relative;
  margin: 0;
  cursor: ew-resize;
}

.map-legend {
  font: 12px/20px "Helvetica Neue", Arial, Helvetica, sans-serif;
  position: absolute;
  width: 440px;
  top: 0;
  left: 0;
  margin: 20px;
  border: 2px solid gray;
  border-radius: 3px;
}

.map-legend .map-legend-inner {
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 10px;
}

@media screen and (max-width: 400px) {
  .map-legend {
    width: 100%;
    margin: 0px;
    border-radius: 0px;
    border: none;
    border-bottom: 1px solid gray;
  }
}

.map-legend-inner fieldset {
  border: none;
  padding: 0;
  margin: 0 0 10px;
}

.map-legend-inner fieldset:last-child {
  margin: 0;
}

.map-legend-inner select {
  width: 100%;
}

.map-legend-inner label {
  display: block;
  font-weight: bold;
  margin: 0 0 5px;
}

.map-legend-inner button {
  display: inline-block;
  width: 36px;
  height: 20px;
  border: none;
  cursor: pointer;
}

.map-legend-inner button:focus {
  outline: none;
}

.map-legend-inner button:hover {
  box-shadow: inset 0 0 0 3px rgba(0, 0, 0, 0.1);
}

#swatches {
  display: flex;
  justify-content: space-around;
  flex: 1;
  align-items: flex-end;
}

.radios {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.radios label {
  margin: 0;
}

#swatches .swatch-color {
  height: 20px;
  width: 30px;
  /* margin-right: 5px; */
  /* display: inline-block; */
}

#swatches > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
}

.legend {
  display: flex;
  justify-content: space-between;
}
